@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0px;
  padding: 0px;
}

@font-face {
  font-family: Blanka;
  src: url(/static/media/Blanka-Regular.0f9bdc30.otf);
}
@font-face {
  font-family: Platform;
  src: url(/static/media/Platform.f9b597c0.ttf);
}
body{
  background: url(/static/media/Background.26e3455c.jpg) no-repeat fixed;
  background-size: cover;
}
.logo-img img{
  width: 60px;
  border: 1px solid gray;
  border-radius: 42px;
}
.app-bar{
  display: flex;
  padding: 7px 12px;
   
  }
  @supports ((-webkit-backdrop-filter: none) or (backdrop-filter: none)) {
    .app-bar{
      -webkit-backdrop-filter: blur(5px);
      backdrop-filter: blur(5px);
    }
  }
.logo-text{
  margin-top: auto;
  margin-bottom: auto;
  font-family: Blanka;
  position: relative;
  top: -8px;
}
.logo-img{
  padding-right: 2pc;
}
.search-bar-container{
  margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
    top: 23vh;
    position: relative;
    background: white;
    width: 62vw;
    height: 57px;
    border-radius: 6px;
    border: 1px solid;
    display: block;
    box-shadow: 0px 4px 32px 1px #000c0ad6;
}
.search-bar input{
  font-size: 23px;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  position: relative;
  top: 1px;
  height: 54px;
  left: 20px;
  width: 53vw;
  font-family: Plus;
  border: none;
}
.search-bar input:focus, textarea:focus, select:focus{
  outline: none;
}
.icons{
    margin-top: auto;
    margin-bottom: auto;
    position: absolute;
    right: 20px;
    top: 25px;
    display: flex;
}
@font-face {
    font-family: Plus;
    src: url(/static/media/Plus.1e5642fd.otf);
}
.title{
    padding: 7px 11px;
    font-family: 'PLATFORM';
}
.sta{
    text-align: center;
    font-size: 19px;
    font-family: 'Roboto';
    position: absolute;
    bottom: 17px;
    padding: 1px 47px;
}
.msg{
    font-family: 'Roboto';
    padding: 20px 16px;
    font-size: 11px;
}
.meaning{
    font-family: 'ROBOTO';
    text-align: center;
    font-size: 15px;
    padding-top: 30px;
}
.title-of{
    text-align: center;
    font-size: 2.5rem;
    padding-top: 25px;
    font-family: platform;
}
.weather-text{
    position: relative;
    top: -19px;
    padding: 34px 13px;
    font-family: Platform;
}
.svg-1-2-26.svg-d1-1-2-28{
    margin-left: auto;
    margin-right: auto;
    fill: white;
    display: block;
}
.widgets{
    position: relative;
    top: 167px;
    padding: 1px 34px;
    display: flex;
    justify-content: center;
}
.widg{
    padding: 0px 40px;
}
.icons svg:hover{
    cursor: pointer;
    transition: 0.4s;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
#avatar:hover{
    cursor: pointer;
}
#avatar_stat{
    margin-left: auto;
    margin-right: auto;
    width: 100px;
    height: 100px;
    position: relative;
    top: 31px;
    font-size: 36px;
}
.details{
    position: absolute;
    bottom: 96px;
    padding: 1px 14px;
    font-size: 18px;
    font-family: 'ROBOTO';
}
.details-info{
    padding: 14px 0px;
}
#Subhere{
    visibility: hidden;
}
.google-button button{
    position: absolute;
    bottom: 18px;
    left: 34px;
}
.blocked-title{
    text-align: center;
    padding: 9px 0px;
    font-family: 'PLATFORM';
}
#blockpaper{
    visibility: hidden;
}
.MuiPaper-root.MuiPaper-elevation1.MuiPaper-rounded{
    width: 215px;
    height: 271px;
    position: relative;
}



@media (max-width:450px) {
     
    .icons{
        display: none;
    }
    .widgets{
        display: block;
    }
    .widg{
        padding: 30px 40px; 
    }
    .MuiPaper-root.MuiPaper-elevation1.MuiPaper-rounded{
        width: 85vw;
        height: 68vh;
        position: relative;
        left: -42px;
    }
    .MuiPaper-root.MuiPaper-elevation1.MuiPaper-rounded{
        width: 85vw;
        height: 68vh;
        position: relative;
        left: -42px;
    }
    .title {
        padding: 15px 26px;
        font-family: 'PLATFORM';
        font-size: 28px;
    }
    .svg-1-2-26.svg-d1-1-2-28{
        width: 204px;
        height: 194px;
    }
    .weather-text {
        position: relative;
        top: -19px;
        padding: 42px 26px;
        font-family: Platform;
        font-size: 21px;
    }
    .title-of {
        text-align: center;
        font-size: 3.5rem;
        padding-top: 25px;
        font-family: platform;
    }
    .meaning {
        font-family: 'ROBOTO';
        text-align: center;
        font-size: 27px;
        padding-top: 30px;
    }
    .msg {
        font-family: 'Roboto';
        padding: 20px 16px;
        font-size: 20px;
    }
    .sta {
        text-align: center;
        font-size: 29px;
        font-family: 'Roboto';
    }
    .search-bar-container {
         
        width: 86vw;
         
    }
}
.results-area{
    background: #fff;
    margin-left: auto;
    margin-right: auto;
    padding: 130px 45px;
    display: flex;
}
.item{
    padding: 8px 11px;
    height: 106px;
    padding-bottom: 52px;
}
.Cardimg{
    display: flex;
}
.Cardar{
    
    width: 300px;
    border: 1px solid black;
    padding: 6px 7px;
    border-radius: 3px;
}
.itemtitle{
    font-size: 26px;
    font-family: 'PLUS';
    color: #000;
}
.snippet{
    font-family: 'Plus';
    width: 58%;
    text-align: justify;
    color: #000;
}
 
.container_web{
    width: 0;
    height: 0;
}
 
.web-icon{
    top: 4px;
    position: relative;
}
iframe{
    height: 100%;
    width: 100%;
}
.link_web:hover + .container_web{
    height: 300px;
    width: 150px;
    color: red;
}

